.custom-layout {
	position: absolute;
    /* top: -100px; */
	inset: 0px;
	pointer-events: none;
	z-index: 300 !important;
}

.custom-toolbar {
    width: 350px;
    position: relative;
    /* top: -100px; */
    border-radius: 10px;
    border: 0.5px solid #CACACA;
    background: #F8F8F8;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
    margin: 10px auto;
}

.custom-tool {
	pointer-events: all;
    width: 40px;
    height: 40px;
    margin: 10px 10px;
}

.custom-tool:hover {
    cursor: pointer;
    background-color: #CACACA;
}

.tldraw-editor {
    position: flex;
    height: 750px;
    width: 800px;
}

