.input-box-button{
	pointer-events: all;
    position: relative;
    width: 150px;
    height: 40px;
    border-radius: 50px;
    border-radius: 50px;
    margin: 15px auto;
    background-color: #007EFF;
    font-size: 24px;
    padding: auto;
    line-height: 40px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25); 
}

.input-box-button:hover {
    cursor: pointer;
    background-color: #0066CC;
}

.input-box-container{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    bottom: 10px;
    height: 100%;
    width: 100%;
    overflow: hidden;
    z-index: 500;
}

.input-box {
    margin: 0 auto;
	pointer-events: all;
    position: relative;
    width: 450px;
    height: 60px;
    padding: 10px;
    line-height: 40px;
    margin-top: 15px;
    padding-left: 20px;
    font-size: 20px;
    border-radius: 50px;
    border: 0.5px solid #CACACA;
    background: #F8F8F8;
    color: black;
    text-align: left;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
    overflow:hidden;
}

