@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.left-container {
  width: 150px;
  height: 100%;
  position: absolute;
  left: 0;
}

.background-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; /* Equal spacing between elements */
  flex-direction: column;
  align-items: center;
  padding-bottom: 70px;
}

.background-bar-item {
  width: 80px;
  height: 80px;
  border-radius: 10%; 
  border: 2px solid #000000;
  margin-bottom: 15px;
	pointer-events: all;
  cursor: pointer; 
}

.background-bar-item.inactive {
  opacity: 0.5;
}

.background-bar-item.active {
  opacity: 1.0;
}

.background-bar-item:hover {
  opacity: 1.0;
}
